.cux-textbox {
  background: transparent;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #808499;

  & .asterisk {
    color: #808499;
  }

  & .asterisk-error {
    color: #ad292f;
  }
}

.cux-textbox-value {
  outline: none;
  color: #ffffff;
  font-size: 14px;
  background: transparent;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #808499;
  margin-bottom: 5px;

  & .error {
    color: #ad292f;
  }
}

.cux-textbox-value:focus {
  outline: none;
  background: transparent;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #ffffff;
  margin-bottom: 5px;
}

.cux-textbox-label {
  outline: none;
  color: #808499;
  font-size: 11px;
  padding-right: 10px;
  margin-bottom: 5px;
}