.cux-notification {
  width: 340px;
  background-color: #33353d;
  -webkit-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  -moz-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  color: #c7c7c7;
  font-size: 14px;
  text-align: center;
  line-height: 15px;
  padding: 20px 20px 20px 20px;

  & .loading {
    margin-bottom: 20px;
  }

  & .message {
    color: #808499;
    font-size: 11px;
    line-height: 15px;
    margin-top: 20px;
  }

}
