.cux-table  {
  display: table;
  border-collapse: collapse;
  width: 100%;
  line-height: 30px;
  background-color: #1f2127;
  font-size: 11px;
  color: #808499;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cux-table-heading {
  display: table-header-group;
  border-bottom: 1px solid #1f2127;
}

.cux-table-body {
  display: table-row-group;
}

.cux-table-row {
  background-color: #292a31;
  display: table-row;
  border-bottom: 1px solid #1f2127;
  color: #aaaecb;
}

.cux-table-row:hover {
  background-color: #3a3c44;
  color: #ffffff;
}

.cux-table-row:focus {
  background-color: #52545f;
  color: #ffffff;
}


.cux-table-cell {
  display: table-cell;
}

.cux-table-head {
  display: table-cell;
  border-right: 1px #3c3f49 dotted;
  padding-left: 10px;
}

.cux-table-foot {
  display: table-footer-group;
}