.cux-progress-bar {
  display: flex;
  justify-content: left; /* align horizontal */
  align-items: center; /* align vertical */
}

.cux-progress-bar-label {
  color: #808499;
  font-size: 11px;
  padding-right: 10px;
}

.cux-bar {
  width: 100%;
  height: 6px;
  border-radius: 2px;
  background-color: #1f2127;
  position: relative;
}

.cux-bar-loading {
  height: 6px;
  position: absolute;
  background-color: #0d87ee;
  border-radius: 2px;
}

.cux-progress-bar-button {
  width: 12px;
  height: 12px;
  padding: 0 10px;
  cursor: pointer;
  background-color: #808499;
  mask: svg('images/progress-bar/cancel.svg');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: 24px;
  -webkit-mask-position: center,center;
}