.cux-btn {
  border: none;
  cursor: pointer;
  height: 30px;
  font-size: 14px;
  text-align: left;
  vertical-align: middle;
  background-color: #363741;
  color: #c7c7c7;
}

.cux-btn:disabled {
  opacity: 0.5;
  cursor: context-menu;
}

.cux-btn:hover:not([disabled]) {
  background-color: #52545f;
  color: #ffffff;
}

.cux-btn:focus {
  background-color: #52545f;
  color: #ffffff;
}

.cux-btn-prim {
  background-color: #0b65b1;
  color: #ffffff;
}

.cux-btn-prim:hover:not([disabled]) {
  background-color: #2375b9;
}

.cux-btn-prim:focus:not([disabled]) {
  background-color: #2375b9;
}