.cux-collapse {
  width: 10px;
  height: 10px;
  background-color: #292a31;
}

.coll-icon {
  background-color: #808499;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}

.coll-icon:hover {
  background-color: #ffffff;
}

.cux-collapse:hover {
  background-color: #52545f;
}

.coll-left {
  mask: svg('images/collapse-icons/chevron-left.svg');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: 5px;
  -webkit-mask-position: center,center;
}

.coll-right {
  mask: svg('images/collapse-icons/chevron-right.svg');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: 5px;
  -webkit-mask-position: center,center;
}

.coll-up {
  mask: svg('images/collapse-icons/chevron-up.svg');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: 10px;
  -webkit-mask-position: center,center;
}

.coll-down {
  mask: svg('images/collapse-icons/chevron-down.svg');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: 10px;
  -webkit-mask-position: center,center;
}