.cux-tooltip {
  background-color: #41434d;
  color: #c7c7c7;
  font-size: 11px;
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  position: relative;
}

.cux-tooltip:before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  border: 4px solid transparent;
  border-bottom-color: #41434d;
}
