input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 12px;
  height: 12px;
  vertical-align: text-bottom;
}

.cux-checkbox {
  @import '../../images/checkbox/checkbox.svg';
  background-repeat: no-repeat;
  outline: none;
  color: #808499;
  font-size: 11px;
  margin: 0 10px;
}

.cux-checkbox:not([checked]) {
  @import '../../images/checkbox/checkbox.svg';
  background-repeat: no-repeat;
  opacity: 1;
}

.cux-checkbox:not([checked]):disabled {
  @import '../../images/checkbox/checkbox.svg';
  background-repeat: no-repeat;
  opacity: 0.5;
}

.cux-checkbox:not([disabled]):checked {
  @import '../../images/checkbox/checkbox_checked.svg';
  background-repeat: no-repeat;
  opacity: 1;
}
