.cux-radio-btn {
  @import '../../images/checkbox/radio.svg';
  background-repeat: no-repeat;
  color: #808499;
  font-size: 11px;
  margin: 0 10px;
  cursor: pointer;
  outline: none;
}

.cux-radio-btn:not([checked]) {
  @import '../../images/checkbox/radio.svg';
  background-repeat: no-repeat;
  opacity: 1;
}

.cux-radio-btn:not([checked]):disabled {
  @import '../../images/checkbox/radio.svg';
  background-repeat: no-repeat;
  opacity: 0.5;
  cursor: default;
}

.cux-radio-btn:not([disabled]):checked {
  @import '../../images/checkbox/radio_checked.svg';
  background-repeat: no-repeat;
  opacity: 1;
}

.cux-radio-btn:checked:disabled {
  @import '../../images/checkbox/radio_checked.svg';
  background-repeat: no-repeat;
  opacity: 0.5;
  cursor: default;
}
