.cux-drop-down {
  position: relative;
  background-color: #41434d;
}

.cux-drop-down:before {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  margin-left: -5px;
  border: 5px solid transparent;
  border-bottom-color: #41434d;
}

.cux-drop-down-header {
  vertical-align: middle;
  display: flex;

  & .arrow {
    display: inline-block;
    margin-left: auto;
    width: 11px;
    height: 11px;
    color: #808499;
    background-color: #41434d;
    mask: svg('images/drop-down/numeric_down-5to8px.svg');
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-size: 11px;
    -webkit-mask-position: center,center;
  }
}

.cux-drop-down-toggle {
  cursor: pointer;
  display: flex;
  align-items: center; /* align vertical */
  flex-grow: 100;
  outline: none;
  color: #ffffff;
  font-size: 14px;
  background: transparent;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #808499;
  margin-bottom: 5px;
}

.cux-drop-down-item {
  color: #c7c7c7;
  font-size: 11px;
  min-height: 30px;
  line-height: 30px;
  padding: 0 20px;
  background-color: #41434d;
  display: flex;
  justify-content: left; /* align horizontal */
  align-items: center; /* align vertical */
}

.cux-drop-down-input-wrapper {
  padding: 0 20px;
  min-height: 30px;
  background-color: #41434d;
}

.cux-drop-down-input {
  outline: none;
  width: 100%;
  color: #ffffff;
  height: 30px;
  background-color: transparent;
  @import '../../images/drop-down/filter.svg';
  background-repeat: no-repeat;
  background-size: 21px 22px;
  background-position: left;
  font-size: 11px;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #808499;
  display: flex;
  justify-content: left; /* align horizontal */
  align-items: center; /* align vertical */
  margin-bottom: 10px;
  padding-left: 23px;
}

.cux-drop-down-input:focus {
  outline: none;
  background: transparent;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #ffffff;
  @import '../../images/drop-down/filter.svg';
  background-repeat: no-repeat;
  background-size: 21px 22px;
  background-position: left;
}

.cux-drop-down-item:hover {
  background-color: #0d87ee;
  color: #ffffff;
}
.cux-drop-down-item:focus {
  background-color: #0d87ee;
  color: #ffffff;
}
