.cux-drag-item {
  width: 270px;
  height: 30px;
  font-size: 11px;
  color: #ffffff;
  opacity: 1;
  border-bottom: 1px solid #1f2127;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  background-color: #52545f;
}

.cux-drag-thumb {
  width: 53px;
  height: 30px;
  display: flex;
}

.cux-drag-data {
  max-height: 30px;
  line-height: 30px;
  padding-left: 10px;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.cux-drop-area-wrapper {
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #0d87ee;
  background-color: rgba(0, 0, 0, 0.5);
}

.cux-drop-area {
  position: relative;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  background: radial-gradient(black 50%, white);;
  left: calc(50% - 16px);
  top: calc(50% - 16px);
}

.cux-drop-icon {
  position: relative;
  height: 32px;
  width: 32px;
  @import '../../images/drag-drop/drop.svg';
}