.cux-dialog {
  background-color: #292a31;
  -webkit-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  -moz-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);

  & .dialog-header {
    background-color: #33353d;
    font-size: 14px;
    color: #c7c7c7;
    text-align: center;
    padding: 20px 20px 20px 20px;
  }

  & .dialog-message {
    font-size: 11px;
    color: #808499;
    line-height: 15px;
  }

  & .dialog-footer {
    background-color: #33353d;
  }

  & .dialog-btn-prim {
    background-color: #0a65b1;
    color: #ffffff;
  }

  & .dialog-btn-prim:hover:not([disabled]) {
    background-color: #2375b9;
    color: #ffffff;
  }

  & .dialog-btn-prim:focus {
    background-color: #2375b9;
    color: #ffffff;
  }

  & .dialog-btn-prim:disabled {
    opacity: 0.5;
  }

  & .dialog-btn-cancel {
    background-color: #464754;
    color: #c7c7c7;
  }

  & .dialog-btn-cancel:hover:not([disabled]) {
    background-color: #52545f;
    color: #ffffff;
  }

  & .dialog-btn-cancel:focus {
    background-color: #52545f;
    color: #ffffff;
  }

  & .dialog-btn-cancel:disabled {
    opacity: 0.5;
  }

  & .dialog-search {
    outline: none;
    background: transparent;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid #808499;
    margin-bottom: 5px;
    color: #ffffff;
  }

  & .dialog-search:focus {
    outline: none;
    background: transparent;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid #ffffff;
  }

  & .dialog-btn-multi-select {
    font-size: 11px;
    padding: 0 10px;
    background-color: #363741;
    color: #c7c7c7;
  }

  & .dialog-btn-multi-select:hover {
    background-color: #52545f;
    color: #ffffff;
  }

  & .dialog-btn-multi-select:focus {
    background-color: #52545f;
    color: #ffffff;
  }

  & .dialog-multi-select-row {
    line-height: 30px;
    display: flex;
    align-items: center;
    font-size: 11px;
  }

  & .dialog-multi-select-row:hover {
    background-color: #0d87ee;
    color: #ffffff;
  }

  & .dialog-multi-select-row:focus {
    background-color: #0d87ee;
    color: #ffffff;
  }
}
