.cux-rclick-menu {
  color: #c7c7c7;
  font-size: 11px;
  background-color: #41434d;
  -webkit-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  -moz-box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  box-shadow: 0 5px 20px 4px rgba(0,0,0,0.2);
  list-style-type: none;
  padding: 0;
}

.cux-rclick-menu > li {
  cursor: pointer;
  line-height: 30px;
  padding: 0 20px;
  display: flex;
  justify-content: left; /* align horizontal */
  align-items: center; /* align vertical */
}

.cux-rclick-menu > li:hover {
  background-color: #0d87ee;
  color: #ffffff;
}

.cux-rclick-menu > li:focus {
  background-color: #0d87ee;
  color: #ffffff;
}