.cux-toolbar {
  height: 40px;
  background-color: #292a31;
  padding: 0 20px 0 20px;

  & button {
    margin: 0 25px;
  }

  & .cux-toolbar-icon {
    background-color: #808499;
    width: 24px;
    height: 24px;
    position: center;
    margin: 0 20px;
    display: inline-block;
  }

  & .cux-toolbar-icon:focus {
    color: #ffffff;
  }

  & .cux-toolbar-icon:hover {
    color: #ffffff;
  }

  & .cux-toolbar-icon:disabled {
    opacity: 0.5;
  }
}